<template>
  <div v-if="info">
    <div class="question-wrapper">
      <h3>{{info.label}}</h3>

      <div class="question">
        <p>{{info.data.question}}</p>
      </div>
      <div class="answer">
        <div class="table-head">

          <b>{{info.data.table_label}}</b>


        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {

  name: 'LBSProtocol',

  props: {
  	info: null,
    tabId:null

  },

  created() {
  },

  data () {
    return {
    }
  },
}
</script>

<style lang="css" scoped>
</style>



